.threats-page {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 16px 0;
  border-bottom: 1px solid #334155;
  margin-bottom: 20px;
}

.header-content h1 {
  color: #f8fafc;
  font-size: 28px;
  font-weight: 700;
  margin: 0 0 4px 0;
}

.header-content p {
  color: #94a3b8;
  font-size: 14px;
  margin: 0;
}

.header-actions {
  display: flex;
  gap: 12px;
}

.btn {
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  gap: 6px;
}

.btn-primary {
  background: #3b82f6;
  color: white;
  border-color: #3b82f6;
}

.btn-primary:hover {
  background: #2563eb;
  border-color: #2563eb;
}

.btn-secondary {
  background: #374151;
  color: #cbd5e1;
  border-color: #4b5563;
}

.btn-secondary:hover {
  background: #4b5563;
  color: #f8fafc;
}

.threats-content {
  flex: 1;
  display: flex;
  gap: 20px;
  min-height: 0;
}

.threats-main {
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
}

.tab-content {
  flex: 1;
  background: #1e293b;
  border: 1px solid #334155;
  border-radius: 8px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.tab-content h3 {
  color: #f8fafc;
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 8px 0;
}

.tab-content p {
  color: #94a3b8;
  font-size: 16px;
  margin: 0;
}

/* Responsive */
@media (max-width: 1024px) {
  .threats-content {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .page-header {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }
  
  .header-actions {
    justify-content: stretch;
  }
  
  .btn {
    flex: 1;
    justify-content: center;
  }
}
